import _ from 'lodash';
import React from 'react';
import PropTypes from 'prop-types';

const Score = ({
  numberCorrect,
  totalQuestions,
}) => {
  const correctVsTotal = `${numberCorrect}/${totalQuestions}`;
  const score = totalQuestions > 0 ? numberCorrect / totalQuestions : 0;
  const scoreAsPercent = _.round(score * 100, 2);
  return (
    <div className="score">
      {`Score: ${scoreAsPercent}% (${correctVsTotal})`}
    </div>
  );
};

Score.propTypes = {
  numberCorrect: PropTypes.number,
  totalQuestions: PropTypes.number,
};

Score.defaultProps = {
  numberCorrect: 0,
  totalQuestions: 0,
};

export default Score;
