
import * as actionTypes from './actionTypes';

const DEFAULT_CONFIG = {
  operations: [
    { symbol: '+', min: 0, max: 9, active: true },
    { symbol: '-', min: 0, max: 9, active: true },
    { symbol: '×', min: 0, max: 9, active: true },
    { symbol: '÷', min: 0, max: 9, active: true },
  ],
  timeBetweenQuestions: 1500,
};

const configReducer = (state = DEFAULT_CONFIG, action) => {
  switch (action.type) {
    case actionTypes.UPDATE_CONFIG:
      return {
        ...state,
        config: action.data,
      };
    default:
      return state;
  }
};

export default configReducer;
