
const randomNumber = (min, max) => Math.floor((Math.random() * (max - min + 1)) + min);

const formatNumber = (number) => {
  const numAsString = number.toString();
  const div3 = Math.floor(numAsString.length / 3);
  const mod3 = numAsString.length % 3;

  let returnValue = numAsString.substring(0, mod3);
  for (let i = 0; i < div3; i += 1) {
    const start = mod3 + 3 * i;
    const end = start + 3;
    const seperatorOn = (i > 0 && div3 > 0) || mod3 > 0;
    returnValue += `${seperatorOn ? ',' : ''}${numAsString.substring(start, end)}`;
  }

  return returnValue;
};

const createSimpleQuestion = (operand, accumulator) => {
  const numbers = [];
  for (let i = 0; i < 2; i += 1) {
    numbers[i] = randomNumber(operand.min, operand.max);
  }

  const question = `${formatNumber(numbers[0])} ${operand.symbol} ${formatNumber(numbers[1])}`;
  const answer = accumulator(numbers[0], numbers[1]);

  return {
    question,
    answer,
  };
};

const createDivisionQuestion = (operand) => {
  const answer = randomNumber(operand.min, operand.max);
  let denominator = randomNumber(operand.min, operand.max);
  while (denominator === 0) {
    denominator = randomNumber(operand.min, operand.max);
  }
  const numerator = answer * denominator;
  const question = `${formatNumber(numerator)} ${operand.symbol} ${formatNumber(denominator)}`;

  return {
    question,
    answer,
  };
};

const createQuestion = (operations) => {
  const activeOperations = operations.reduce((acc, cur) => {
    if (cur.active) {
      acc.push(cur);
    }

    return acc;
  }, []);
  const operand = activeOperations[randomNumber(0, activeOperations.length - 1)];

  switch (operand.symbol) {
    case '+':
      return createSimpleQuestion(operand, (a, b) => a + b);

    case '-':
      return createSimpleQuestion(operand, (a, b) => a - b);

    case '×':
      return createSimpleQuestion(operand, (a, b) => a * b);

    case '÷':
      return createDivisionQuestion(operand);

    default:
      return null;
  }
};

export default {
  randomNumber,
  createQuestion,
};
