import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import Modal from '../Modal';
import Operation from './Operation';

import '../../App.css';


class Config extends Component {
  state = { show: false };

  handleClick = () => {
    this.setState({ show: true });
  }

  handleClose = () => {
    this.setState({ show: false });
  }

  handleOperationChange = (event, index) => {
    const { config, onChange } = this.props;
    const updatedOperations = config.operations;
    const { id, value, type } = event.target;
    const element = id.split('-')[0];
    let setValue;

    switch (type.toLowerCase()) {
      case 'checkbox':
        setValue = !updatedOperations[index][element];
        break;
      case 'number':
        setValue = value.length > 0 ? Number.parseInt(value, 10) : 0;
        break;
      default:
        setValue = value;
        break;
    }

    updatedOperations[index][element] = setValue;

    onChange({
      ...config,
      operations: updatedOperations,
    });
  };

  render() {
    const { config } = this.props;
    const { show } = this.state;
    return (
      <Fragment>
        <FontAwesomeIcon icon="cog" onClick={this.handleClick} />
        <Modal show={show} onClose={this.handleClose}>
          <p>Config</p>
          {config.operations.map((operation, index) => (
            <Operation
              index={index}
              key={operation.symbol}
              operation={operation}
              onChange={(event) => { this.handleOperationChange(event, index); }}
            />
          ))}
        </Modal>
      </Fragment>
    );
  }
}

Config.propTypes = {
  config: PropTypes.shape({
    operations: PropTypes.arrayOf(PropTypes.shape()),
    timeBetweenQuestions: PropTypes.number,
  }).isRequired,
  onChange: PropTypes.func,
};

Config.defaultProps = { onChange: () => {} };

export default Config;
