import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';

const initialState = {
  userAnswer: '',
  resultString: '',
  displayResult: false,
  correct: false,
};

class Question extends Component {
  constructor(props) {
    super(props);

    this.state = initialState;
  }

  componentDidUpdate(incomingProps) {
    if (!_.isEqual(incomingProps, this.props)) {
      this.onUpdate();
      this.answerInput.focus();
    }
  }

  onUpdate = () => {
    this.setState(initialState);
  }

  createResult = (question, userAnswer, answer) => (
    {
      question,
      userAnswer,
      answer,
      correct: userAnswer === answer,
    }
  );

  checkAnswer = (result) => {
    if (result.correct) {
      return {
        resultString: 'You are correct',
        displayResult: true,
        correct: result.correct,
      };
    }
    return {
      resultString: 'That is not the correct answer',
      displayResult: true,
      correct: result.correct,
    };
  }

  handleChange = (event) => {
    const value = event.target.value.trim();
    this.setState({
      userAnswer: value,
      displayResult: false,
    });
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const { userAnswer } = this.state;
    const { timeBetweenQuestions } = this.props;

    if (Number.isNaN(userAnswer)) {
      this.setState({
        resultString: `Error '${userAnswer}' is not a number`,
        displayResult: true,
      });
    } else {
      const { answer, question, onQuestionAnswered } = this.props;
      const result = this.createResult(question, Number(userAnswer), answer);
      this.setState(this.checkAnswer(result));
      setTimeout(() => {
        onQuestionAnswered(result);
      }, timeBetweenQuestions);
    }
  };

  render() {
    const { question } = this.props;
    const { userAnswer, displayResult, resultString, correct } = this.state;

    return question.length ? (
      <form className="question-container" onSubmit={this.handleSubmit}>
        <div className="question">
          {`${question}`}
        </div>
        <div className="answer">
          <input
            type="number"
            ref={(input) => { this.answerInput = input; }}
            value={userAnswer}
            onChange={this.handleChange}
          />
          <input
            type="submit"
            value="Submit"
          />
        </div>
        <div className={`result ${correct ? 'correct' : ''}`}>
          { displayResult
            ? resultString
            : null
          }
        </div>
      </form>
    ) : null;
  }
}

Question.propTypes = {
  answer: PropTypes.number,
  question: PropTypes.string,
  onQuestionAnswered: PropTypes.func,
  timeBetweenQuestions: PropTypes.number,
};

Question.defaultProps = {
  answer: undefined,
  question: '',
  onQuestionAnswered: () => {},
  timeBetweenQuestions: 2000,
};

export default Question;
