import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import Header from '../../components/Header';
import Question from '../../components/Question';
import History from '../../components/History';
import Score from '../../components/Score';
import utils from '../../utils';
import '../../App.css';

import { updateConfig as updateConfigAction } from './actions';


class MathPractice extends Component {
  constructor(props) {
    super(props);

    this.state = {
      question: '',
      answer: undefined,
      numberCorrect: 0,
      history: [],
    };
  }

  componentDidMount() {
    const { config } = this.props;
    const question = utils.createQuestion(config.operations);
    this.setState({ ...question });
  }

  handleQuestionAnswered = (result) => {
    const { config } = this.props;
    const newQuestion = utils.createQuestion(config.operations);
    this.setState(prevState => ({
      history: [
        ...prevState.history,
        {
          number: (prevState.history.length + 1),
          ...result,
        },
      ],
      numberCorrect: prevState.numberCorrect + (result.correct ? 1 : 0),
      ...newQuestion,
    }));
  };

  handleConfigChange = (config) => {
    this.props.updateConfig(config);
  }

  handleReset = () => {
    this.setState({ history: [] });
  }

  render() {
    const { config } = this.props;
    const { question, answer, numberCorrect, history } = this.state;
    return (
      <div>
        <Header
          config={config}
          onConfigChange={this.handleConfigChange}
        />
        <Score
          numberCorrect={numberCorrect}
          totalQuestions={history.length}
        />
        <div className="app-item">
          <Question
            question={question}
            answer={answer}
            onQuestionAnswered={this.handleQuestionAnswered}
            timeBetweenQuestions={config.timeBetweenQuestions}
          />
        </div>
        <History history={history} />
      </div>
    );
  }
}

MathPractice.propTypes = {
  config: PropTypes.shape(),
  updateConfig: PropTypes.func,
};

MathPractice.defaultProps = {
  config: {},
  updateConfig: () => {},
};

const mapStateToProps = (state) => {
  return ({
    config: state.config,
  });
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    updateConfig: updateConfigAction,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(MathPractice);
