import React from 'react';
import PropTypes from 'prop-types';

const Operation = ({
  index,
  operation,
  onChange,
}) => {
  const { symbol, min, max, active } = operation;

  return (
    <div className="config-row">
      <div className="config-row-item">
        <label htmlFor={`active-${index}`} className="switch">
          <input
            id={`active-${index}`}
            type="checkbox"
            onChange={onChange}
            checked={active}
          />
          <span className="slider round" />
        </label>
      </div>
      <div className="config-row-item">
        <span className="symbol">
          {symbol}
        </span>
      </div>
      <div className="config-row-item">
        <label htmlFor="min">
          <span className="label-text">
            {'Min:'}
          </span>
          <input
            className="medium-integer"
            id={`min-${index}`}
            type="number"
            value={min.toString()}
            onChange={onChange}
          />
        </label>
      </div>
      <div className="config-row-item">
        <label htmlFor="max">
          <span className="label-text">
            {'Max:'}
          </span>
          <input
            className="medium-integer"
            id={`max-${index}`}
            type="number"
            value={max.toString()}
            onChange={onChange}
          />
        </label>
      </div>
    </div>
  );
};

Operation.propTypes = {
  index: PropTypes.number.isRequired,
  operation: PropTypes.shape({
    symbol: PropTypes.string,
    min: PropTypes.number,
    max: PropTypes.number,
    active: PropTypes.bool,
  }),
  onChange: PropTypes.func,
};

Operation.defaultProps = {
  operation: {
    symbol: '',
    min: 0,
    max: 0,
    active: true,
  },
  onChange: () => {},
};

export default Operation;
